import React from "react";
import { Link } from "react-router-dom";
import SettingsIcon from "@mui/icons-material/Settings";

function Nav(props) {
  var settingsLink = props.settings ? window.location.pathname : "/settings"
  return (
    <div className="nav">
    <Link className="homenav" to="/">
    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 300 300">
    <polygon fill="var(--highlight)" points="300,150 225,280 75,280 0,150 75,20 225,20" />
    </svg>
    </Link>
    <Link className="settings" to={settingsLink}>
    <SettingsIcon />
    </Link>
    </div>
  );
}

export default Nav;