import React from "react";
import Obfuscate from "../../components/obfuscate.js";

function Appearance() {
const [customColor, setCustomColor] = React.useState({
  background: getComputedStyle(document.body).getPropertyValue('--background').replaceAll(" ", ""),
  highlight: getComputedStyle(document.body).getPropertyValue('--highlight').replaceAll(" ", ""),
  text: getComputedStyle(document.body).getPropertyValue('--text').replaceAll(" ", ""),
  textContrast: getComputedStyle(document.body).getPropertyValue('--text-contrast').replaceAll(" ", ""),
  placeholder: getComputedStyle(document.body).getPropertyValue('--placeholder').replaceAll(" ", "")
})

  return (
      <React.Fragment>
      <div className="optiontitle"><Obfuscate>Theme</Obfuscate></div>
      <div className="chooseoption">
      <div className="optionchoose chooseactive"><Obfuscate>Default</Obfuscate></div>
      <div className="optionchoose"><Obfuscate>Dark</Obfuscate></div>
      <div className="optionchoose"><Obfuscate>Light</Obfuscate></div>
      <div className="optionchoose"><Obfuscate>Lime</Obfuscate></div>
      <div className="optionchoose"><Obfuscate>Space</Obfuscate></div>
      <div className="optionchoose"><Obfuscate>Molten</Obfuscate></div>
      <div className="optionchoose"><Obfuscate>Retro</Obfuscate></div>
      <div className="optionchoose"><Obfuscate>Midnight</Obfuscate></div>
      <div className="optionchoose"><Obfuscate>Tsunami</Obfuscate></div>
      </div>
      {/*
      <div className="optiontitle"><Obfuscate>Custom</Obfuscate></div>
      <div className="allchoosecolor">
      <div className="choosecolor" style={{width: "120px"}}>
      <div className="choosecolortext">Background</div>
      <input className="choosecolorpicker" type="color" value={customColor.background} onChange={e => setCustomColor(e.target.value)} />
      </div>
      <div className="choosecolor" style={{width: "100px"}}>
      <div className="choosecolortext">Highlight</div>
      <input className="choosecolorpicker" type="color" value={customColor.highlight} onChange={e => setCustomColor(e.target.value)} />
      </div>
      <div className="choosecolor" style={{width: "50px"}}>
      <div className="choosecolortext">Text</div>
      <input className="choosecolorpicker" type="color" value={customColor.text} onChange={e => setCustomColor(e.target.value)} />
      </div>
      <div className="choosecolor" style={{width: "130px"}}>
      <div className="choosecolortext">Text Contrast</div>
      <input className="choosecolorpicker" type="color" value={customColor.textContrast} onChange={e => setCustomColor(e.target.value)} />
      </div>
      <div className="choosecolor" style={{width: "100px"}}>
      <div className="choosecolortext">Placeholder</div>
      <input className="choosecolorpicker" type="color" value={customColor.placeholder} onChange={e => setCustomColor(e.target.value)} />
      </div>
      </div>
      */}
      </React.Fragment>
  );
}

export default Appearance;