import React from "react";
import AppendHead from 'react-append-head';

function Head(props) {
var children = props.children || <br />

var defaultTitle = props.defaultTitle || ""
document.title = defaultTitle

document.body.setAttribute("appearance", "default")
return (
    <AppendHead>
    <link rel="icon" href="/images/logo.svg" />
    <script src="/uv/uv.bundle.js" order="0"></script>
    <script src="/uv/uv.config.js" order="1"></script>
    <script src="/stomp/bootstrapper.js" order="2"></script>
    <script src="/dip/dip.config.js" order="3"></script>
    <script src="/osana/config.js" order="4"></script>
    <script src="/js/index.js" order="5"></script>
    {children}
    </AppendHead>
)
}

export default Head;